*{
    margin:0px;
    padding:0px;
    box-sizing:border-box;
}
body{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: darkcyan;
    color:white;
}
.box{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}
.innerBox{
    background-color: wheat;
    height:500px;
    width:350px;
    color:black;
 
    border-radius: 10px;
    box-shadow: 0px 0px 10px 2px gray;
}

h1{
    background-color: tomato;
    color:white;
    text-align: center;
    padding:8px 0px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px 0px gray;
}
.contentArea{
    padding:10px;

}
.contentArea button{
    background-color: red;
    margin: 0px;
    padding: 0px 8px;
    font-size: 30px;
    color:white;
    border-radius: 20px;
    border: none;
    transition: 0.5s;

}
.contentArea button:hover{
    background-color: green;
    transition: 0.5s;
}
.contentArea{
    display: flex;
}
.contentArea input{
    display: block;
    background-color: wheat;
    border: none;
    outline:none;
    border-bottom: 2px solid tomato;
    padding:0px 10px;
    font-size: 20px;
   
}
.listArea{
    padding:0px 20px;
}
.listArea ul{
    list-style-type: none;
    font-size: 25px; 
      
}
.listArea li{
    box-shadow: 0px 0px 10px 0px gray;
    padding:5px 10px;
    border-radius: 10px;
    position: relative;
}
.listArea li span{
    position: absolute;
    right:20px;
    top:8px;
    background-color: green;
    padding:0px 5px;
    color:white;
    border-radius: 20px;
    font-size: 20px;
    cursor:pointer;
    box-shadow: 0px 0px 10px 2px gray;
}